*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

ul {
  list-style: none;
}

img, video, embed, object {
  max-width: 100%;
}

img {
  vertical-align: middle;
}

label, button {
  cursor: pointer;
}

.clearfix::before, .clearfix::after, .grid::before, .grid::after {
  content: "";
  display: table;
}

.clearfix::after, .grid::after {
  clear: both;
}

.ma {
  margin-left: auto;
  margin-right: auto;
}

.site-width {
  max-width: 75em;
}

.ac {
  text-align: center;
}

html, body {
  background: #FFF;
  color: #000;
}

body, input, select, textarea, button {
  font-family: 'Raleway','Arapey', "Open Sans", sans-serif;
  font-size: 100%;
}

.page-nav {
  background: #15263e;
  color: #FFF;
  text-align: center;
  padding: 1em 0 0;
}

@media (min-width: 48em) {
  .page-nav {
    padding: 1.5em;
  }
}

.page-nav__li {
  position: relative;
  border-bottom: 1px solid #CCC;
}

@media (min-width: 48em) {
  .page-nav__li {
    border: 0;
    display: inline-block;
  }
  .page-nav__li::after {
    position: absolute;
    top: 50%;
    right: -0.15em;
    transform: translate(0, -50%);
  }
  .page-nav__li:last-child::after {
    display: none;
  }
}

.page-nav__item {
  display: block;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  transition: opacity 0.2s ease;
  padding: 0.5em 1em;
}

.page-nav__item:hover {
  opacity: 0.7;
}

@media (min-width: 48em) {
  .page-nav__item {
    font-size: 0.875em;
    padding: 1em 2em;
  }
}

.page-nav__hamburger {
  display: inline-block;
  cursor: pointer;
  transition: transform 0.2s ease;
  margin-bottom: 1em;
}

.page-nav__hamburger:hover {
  transform: scale(1.2);
}

@media (min-width: 48em) {
  .page-nav__hamburger {
    display: none;
  }
}

.page-nav__ul {
  display: none;
}

@media (min-width: 48em) {
  .page-nav__ul {
    display: block !important;
  }
}

.section {
  margin-bottom: 1.5em;
  padding: 0 1.25em;
}

@media (min-width: 48em) {
  .section {
    margin-bottom: 2em;
  }
}

@media (min-width: 75em) {
  .section {
    padding: 0;
    margin-bottom: 3em;
  }
}

.section__title {
  text-transform: uppercase;
  color: #15263e;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2em;
}

@media (min-width: 48em) {
  .section__title {
    font-size: 2.625em;
  }
}

@media (min-width: 75em) {
  .section__title {
    font-size: 3.125em;
  }
}

.section__intro-text {
  color: #4d4d4d;
  text-align: center;
  font-size: 0.875em;
  margin-bottom: 1.5rem;
}

@media (min-width: 48em) {
  .section__intro-text {
    font-size: 1em;
    margin-bottom: 3rem;
  }
}

@media (min-width: 75em) {
  .section__intro-text {
    font-size: 1.125em;
    margin-bottom: 4rem;
  }
}

.page-footer {
  background: #dcdcde;
  padding: 6.25em 1.5em;
}

.footer-module {
  margin-bottom: 3em;
  font-size: 0.875em;
}

.footer-module__icon, .footer-module__content {
  display: inline-block;
  vertical-align: middle;
}

@media (min-width: 48em) {
  .footer-module__icon, .footer-module__content {
    vertical-align: top;
  }
}

.footer-module__icon {
  background: #374362;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 100%;
  position: relative;
  font-size: 1.25em;
  color: #FFF;
}

.footer-module__icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 48em) {
  .footer-module__icon {
    width: 2.0625rem;
    height: 2.0625rem;
    font-size: 1.625em;
  }
}

.footer-module__content {
  color: #4d4d4d;
  margin-left: .875em;
}

.footer-module__content a {
  color: inherit;
  text-decoration: none;
}

.footer-module__content a:hover {
  text-decoration: underline;
}

.footer-module__title {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: .875em;
  font-size: 1em;
}

