.welcome {
  background: #15263e url(../images/custom/8-copy.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
  height: 29.375em;
}

@media (min-width: 48em) {
  .welcome {
    height: 47.5em;
  }
}

@media (min-width: 75em) {
  .welcome {
    height: 56.25em;
  }
}

@media (min-width: 100em) {
  .welcome {
    height: 65em;
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2 / 1), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .welcome {
    background-image: url(../images/custom/8-copy.jpg);
  }
}

.welcome__title {
  font-size: 3.25rem;
  text-transform: uppercase;
  font-weight: 300;
  color: #8e191c;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
  line-height: 1;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translate(-10%, -50%);
}

@media (min-width: 48em) {
  .welcome__title {
    font-size: 4rem;
  }
}

@media (min-width: 75em) {
  .welcome__title {
    font-size: 5.75rem;
  }
}

.welcome__title-complement {
  font-size: 1.625rem;
  text-transform: none;
  display: block;
  font-weight: 600;
}

@media (min-width: 48em) {
  .welcome__title-complement {
    font-size: 1.875rem;
  }
}

@media (min-width: 75em) {
  .welcome__title-complement {
    font-size: 2.375rem;
  }
}

.products__item {
  text-align: center;
  margin-bottom: 2em;
  font-size: 0.875em;
  min-height: 250px;
}

@media (min-width: 48em) {
  .products__item {
    margin-bottom: 3em;
    font-size: 1em;
    min-height: 333px;
  }
}

@media (min-width: 75em) {
  .products__item {
    margin-bottom: 4em;
    font-size: 1.125em;
  }
}

.products__item-icon {
  font-size: 2.8125em;
  display: inline-block;
  width: 7.1875rem;
  height: 7.1875rem;
  background: linear-gradient(45deg, #b119ec, #f8027e);
  border-radius: 100%;
  margin-bottom: 1.3rem;
  position: relative;
  color: #FFF;
}

.products__item-icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.products__item-image-container {
  margin-bottom: 1.5em;
}

.products__item-image-container a {
  display: block;
  border: 3px solid transparent;
  transition: border 0.2s ease;
}

.products__item-image-container a:hover {
  border-color: #a91aef;
}

.products__item-image-container img {
  max-width: none;
}

.products__item-title {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.3rem;
  text-transform: uppercase;
  color: #4d4d4d;
}

.products__title-complement {
  display: block;
  font-size: 1.375em;
}

.products__item-description {
  color: #494949;
  line-height: 1.4;
}

.products__item-li {
  display: inline;
  list-style: none;
}

.products__item-li::before {
  content: " \2022";
  margin-right: 0.2em;
  padding: 0 2px;
}

.products__item-li a {
  color: inherit;
  text-decoration: none;
}

.products__item-li a:hover {
  text-decoration: underline;
}

.portfolio__nav {
  text-align: center;
  margin-bottom: 2em;
}

@media (min-width: 48em) {
  .portfolio__nav {
    text-align: left;
  }
}

.portfolio__nav-li {
  display: inline-block;
}

@media (min-width: 48em) {
  .portfolio__nav-li {
    display: block;
  }
}

.portfolio__nav-item {
  color: #4d4d4d;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s ease;
  display: inline-block;
  font-size: 1em;
  margin: 0.3em 0.8em;
}

.portfolio__nav-item:hover, .portfolio__nav-item.is-active {
  color: #a91aef;
}

@media (min-width: 48em) {
  .portfolio__nav-item {
    display: block;
    font-size: 1.375em;
    margin-bottom: 1.25em;
  }
}

.portfolio__item {
  display: block;
  margin-bottom: 2em;
  text-decoration: none;
  border: 3px solid transparent;
  transition: border 0.2s ease;
}

.portfolio__item:hover {
  border-color: #a91aef;
}

.portfolio__item img {
  max-width: none;
  width: 100%;
}

.blog__item {
  font-size: 0.875em;
  margin-bottom: 2em;
}

@media (min-width: 48em) {
  .blog__item {
    font-size: 1em;
    margin-bottom: 3em;
  }
}

@media (min-width: 75em) {
  .blog__item {
    font-size: 1.125em;
    margin-bottom: 4em;
  }
}

.blog__item-image-container {
  margin-bottom: 1.5em;
}

.blog__item-image-container a {
  display: block;
  border: 3px solid transparent;
  transition: border 0.2s ease;
}

.blog__item-image-container a:hover {
  border-color: #a91aef;
}

.blog__item-image-container img {
  max-width: none;
  width: 100%;
}

.blog__item-header, .blog__item-excerpt {
  padding: 0 3px;
}

.blog__item-tag {
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  color: #929292;
  margin-bottom: 0.5em;
  text-decoration: none;
}

.blog__item-tag:hover {
  text-decoration: underline;
}

.blog__item-title {
  font-weight: bold;
  color: #15263e;
  font-size: 1.25em;
  margin-bottom: 1rem;
}

.blog__item-title a {
  color: inherit;
  text-decoration: none;
}

.blog__item-title a:hover {
  text-decoration: underline;
}

.blog__item-excerpt {
  color: #494949;
  line-height: 1.9;
}

.blog__item-excerpt p {
  margin-bottom: 1.5em;
}

.blog__item-read {
  display: block;
  text-align: right;
  color: #a91aef;
  font-weight: bold;
  text-decoration: none;
}

.blog__item-read:hover {
  text-decoration: underline;
}

.blog__item-read::after {
  content: " \00bb";
}

.contact {
  background: #15263e;
  margin-bottom: 0;
}

.contact .section__title, .contact .section__intro-text {
  color: #FFF;
}

.contact__wrapper {
  max-width: 61.125em;
  padding: 2.5em 0;
}

@media (min-width: 48em) {
  .contact__wrapper {
    padding: 4.6875em 0;
  }
}

@media (min-width: 75em) {
  .contact__wrapper {
    padding: 9.375em 0;
  }
}

.contact-form {
  font-size: 0.875em;
}

@media (min-width: 48em) {
  .contact-form {
    font-size: 1em;
  }
}

@media (min-width: 75em) {
  .contact-form {
    font-size: 1.125em;
  }
}

.contact-form__container {
  margin-bottom: 0.75em;
}

.contact-form__label {
  display: block;
  color: #FFF;
  margin-bottom: 0.2em;
}

.contact-form__field {
  display: block;
  padding: 0.5em;
  color: #4d4d4d;
  border: 0;
  background: #FFF;
  width: 100%;
  transition: background 0.2s ease;
  border-radius: 0;
  -webkit-appearance: none;
}

.contact-form__field:focus {
  background: #FFC;
}

.contact-form__field--message {
  height: 8em;
  margin-bottom: 2em;
}

.contact-form__button {
  font-weight: bold;
  border: 0;
  color: #FFF;
  text-transform: uppercase;
  background: linear-gradient(90deg, #b119ec, #f8027e);
  padding: 1em 2em;
  transition: transform 0.2s ease;
}

.contact-form__button:hover {
  transform: scale(1.1);
}
